import { FC } from 'react';

import { ButtonProps, ButtonVariant, ComponentScope } from './button.interface';

import { borderRadius } from './../../../tailwind/styles/border-radius';
import { focusRing } from './../../../tailwind/styles/focus';


export const Button: FC<ButtonProps> = ({
  variant = ButtonVariant.Primary,
  label,
  isLoading = false,
  children,
  disabled = false,
  ...props
}: ButtonProps) => {
  const loading = isLoading ? <i className="mr-zep-0_5" data-testid="loading-icon">loading icon</i> : '';

  return (
    <button 
      className={`
      ${ComponentScope} 
      zep-${variant} 
      ${disabled ? 'disabled cursor-not-allowed' : ''} 
      ${focusRing} focus-within:ring-offset-2 
      ${borderRadius}
      leading-zep-1_25
      px-zep-1 py-zep-0_75 
      text-zep-base`} 
      {...props}>
      {loading}
      {label || children}
    </button>
  );
};
